
.knowledge-area{
    margin-top: 20px;
}

.hire-me-btn{
    background: #961b1b !important;
    color: #fff !important;
}

.knowledge-area h2{
    text-align: center;
    font-size: 2rem;
}

.hire-me-cont{
    margin-top: 20px;
    text-align: center;
}

.flex{
    display: flex;
    justify-content: space-around;
}

.navbar{
    height: 70px;
    border-bottom: 1px solid #F8EDED;
    display: flex;
    justify-content: center;
}

.nav-links{
    display: flex;
    width: 90%;
    margin: auto;
}

.nav-link{
    list-style: none;
    position: relative;
}

.nav-link a{
    text-decoration: none;
    color: #fff;
    font-size: 1.3rem;
}

.nav-links .right{
    margin-left: auto !important;
}

.btn-pink{
    padding: 10px;
    color: #961b1b !important;
}

.btn-pink:hover{
    background: rgba(255, 255, 255, 0.2);
}

.underline-1{
    border-top: 1px solid #ddd;
    position: absolute;
    width: 50px;
    animation: underline-movement-1-coming 1s ease-in;
}

@keyframes underline-movement-1-coming{
    from{
        margin-left: -300%;
    }
    to{
        margin-left: 0;
    }
}

.underline-2{
    border-top: 1px solid #ddd;
    position: absolute;
    width: 70px;
    animation: underline-movement-2-coming 1s ease-in;
}

@keyframes underline-movement-2-coming{
    from{
        right: -300%
    }
    to{
        right: 0;
    }
}

.content{
    height: calc(100vh - 70px);
    position: relative;
    overflow: hidden;
    display: flex;
}

.main-content{
    width: 90%;
    height: 100%;
    display: flex;
    margin: auto;
}

.footer{
    position: fixed;
    bottom: 0;
    height: 70px;
    width: 100%;
    display: flex;
}

.left-content, .right-content{
    margin: auto;
    width: 50%;
}

.right-content{
    margin-top: -10px;
}

@media screen and (max-width:950px) {
    .main-content{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column-reverse;
        margin: auto;
    }

    .content{
        overflow-y: scroll !important;
    }

    .right-content, .left-content{
        height: 50%;
    }
}

.arrow{
    position: fixed;
    height: 70px;
}

.next-arrow{
    top: 50%;
    right: 0;
}

.left-sidebar{
    position: fixed;
    width: 30vw;
    border-right: 1px solid #F8EDED;
    border-bottom: 1px solid #F8EDED;
    border-radius: 5px;
    text-align: center;
    height: 80vh;
    box-shadow: 0 0 5px 0 #fff;
    overflow-y: scroll;
}

.demo-main-content{
    margin-left: 35vw;
    display: flex;
    flex-direction: column;
    width: 100%;
    overflow: scroll;
}

.left-sidebar-header-text{
    font-size: 1.3rem;
    font-weight: lighter;
    margin-top: 20px;
}

.rule{
    margin-top: 20px;
    border-bottom: 1px solid #ddd;
}

.category-link{
    text-decoration: none;
    color: #fff;
    display: block;
    font-size: 1.3rem;
    margin-top: 20px;
}

.category-link:hover{
    border-bottom: 1px solid #fff;
}

.category-title{
    text-align: center;
}

.category-list{
    display: flex;
    flex-wrap: wrap;
}

.card-link{
    width: 300px;
    height: 350px;
    text-decoration: none;
    margin: auto;
}

.card{
    display: flex;
    flex-direction: column;
    outline: none;
    transition: all 0.3s ease-in !important;
}

.card:hover{
    transform: scale(1.02);
}

.card-header{
    background: #fff;
    color: #000;
    text-align: center;
}

.card-title{
    font-weight: normal;
}

.list-elements{
    display: flex;
    justify-content: space-evenly;
}

.list-element{
    list-style: none;
    color: #961b1b;
    text-decoration: underline;
}

.category-links-mobile{
    margin: 10px;
}

.category-links-mobile button{
    margin: 5px;
}

@media screen and (max-width: 550px){
    .demo-main-content{
        margin-left: 0;
    }

    .content{
        flex-direction: column;
        height: 78vh;
    }
}

.social-icons{
    display: flex;
    align-items: center;
}

.social-icons li{
    list-style: none;
}

.social-icons svg{
    font-size: 3rem;
    background: #000;
}

.social-icons a{
    transition: all 0.2s ease-in-out;
}

.social-icons a:hover{
    color: #009;
}

.bg-particles{
    position: absolute;
}

.single-demo{
    width: 90%;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 12vh;
}

.single-img{
    width: 70vw;
    height: 50vh; 
    object-fit: cover;
}

.visit-btn{
    margin-left: 15px;
}

.alice-carousel__prev-btn, .alice-carousel__next-btn{
    width: 30px !important;
    font-size: 1.5rem;
}

.slide-img{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.react-responsive-modal-modal{
    width: 70vw;
    height: 500px;
}

.modal-form{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 20px;
}

.form-area{
    width: 100%;
    margin-bottom: 10px;
    min-height: 30px;
}

.form-area input{
    width: 90%;
    display: block;
    height: 30px;
}

.form-area textarea{
    display: block;
    width: 90%;
    height: 200px;
}